import Image from "next/image";

export default function Header() {
  return (
    <header className="p-6 flex items-center justify-between bg-green-500 text-white border-2 border-blue-700 rounded-xl shadow-lg">
      <h1 className="text-4xl font-bold italic">Logo</h1>

      <div className="flex items-center gap-4">
        <Image
          src="https://www.pngmart.com/files/21/Account-User-PNG-HD.png"
          width={64}
          height={64}
          className="w-16 h-16 rounded-full border-2 border-white transition-transform transform hover:scale-110"
          alt="Account image"
        />
        <div>
          <h2 className="text-2xl font-semibold hover:underline">Firdavs</h2>
          <span className="text-sm opacity-75">123456</span>
        </div>
      </div>
    </header>
  );
}
